<template>
	<Swiper class="diary-detail-container" @swiper="onSwiper" @slideChange="onSlideChange" :space-between="12">
		<swiper-slide class="record-detail-item" v-for="(item, index) in data.list" :key="index">
			<div class="title">{{item.date}}</div>
			<div class="record-list">
				<div class="record-item" v-for="(v, i) in item.record" :key="i">
					<div class="type">斗地主</div>
					<div v-if="v.result === 1" class="result success">胜</div>
					<div v-if="v.result === 0" class="result fail">负</div>
					<div class="detail-container">
						<div class="detail detail-first">地主:
							<span v-if="v.dizhu.isMine && v.dizhu.success === 1"
								class="mine success">{{v.dizhu.name}}</span>
							<span v-else-if="v.dizhu.isMine && v.dizhu.success === 0"
								class="mine fail">{{v.dizhu.name}}</span>
							<span v-else>{{v.dizhu.name}}</span>
						</div>
						<div class="detail detail-second">农民:
							<span v-for="(vv, ii) in v.nongmin" :key="ii"
								:class="{'mine': vv.isMine, 'success': vv.isMine && vv.success === 1,'fail': vv.isMine && vv.success === 0}">{{vv.name}}</span>
						</div>
					</div>
					<img :src="v.icon" class="photo" alt="">
				</div>
			</div>
		</swiper-slide>
	</Swiper>
</template>

<script setup>
	import {
		onMounted,
		reactive
	} from 'vue';
	import {
		Swiper,
		SwiperSlide
	} from 'swiper/vue';
	import {
		useRoute
	} from 'vue-router';
	import {
		diaryDataObj
	} from '@/mock/diary/data';

	const route = useRoute();
	const dataDate = route.query.date;
	const index = route.query.index | 0;

	const onSwiper = (swiper) => {
		console.log(swiper);
	};
	const onSlideChange = () => {
		console.log('slide change');
	};

	const data = reactive({
		list: []
	})

	onMounted(() => {
		if (dataDate) {
			data.list = diaryDataObj[dataDate];
		} else {
			// 如果没带数据则取当月第一个
			const year = new Date().getFullYear();
			const month =  Number(new Date().getMonth() + 1);
			const currentDate = year + '' + month;
			data.list = diaryDataObj[currentDate];
		}
		setTimeout(() => {
			const swiper = document.querySelector('.swiper')?.swiper;
			swiper.slideTo(index)
		}, 200)
		
	})
</script>

<style lang="less" scoped>
	.diary-detail-container {
		height: 100%;
		background-color: rgba(0, 140, 140, 0.75);
		border-radius: 12px;
		overflow: hidden;
		padding-top: 12px;

		.record-detail-item {
			height: 100%;
			display: flex;
			flex-direction: column;
			overflow: hidden;
		}

		.title {
			text-align: center;
			font-size: 20px;
			flex-shrink: 0;
		}

		.record-list {
			flex-grow: 1;
			overflow-y: auto;
			margin-top: 12px;
			padding: 0 12px 12px 12px;

			.record-item {
				display: flex;
				align-items: center;
				padding: 12px;
				background-color: rgba(255, 255, 255, 0.78);
				box-shadow: 0 0 3px #dbdbdb;
				border-radius: 8px;
				position: relative;

				.photo {
					position: absolute;
					right: 8px;
					top: 0;
					height: 100%;
				}

				&:not(:nth-of-type(1)) {
					margin-top: 12px;
				}

				.type {}

				.result {
					font-size: 20px;
					width: 60px;
					text-align: center;

					&.success {
						color: #db2a2f;
					}

					&.fail {
						color: #51dd51;
					}
				}

				.detail-container {
					flex-grow: 1;

					.detail {
						span {
							display: inline-block;

							&:not(:last-child) {
								margin-left: 4px;
							}

							&.mine {
								&.success {
									color: #db2a2f;
								}

								&.fail {
									color: #51dd51;
								}
							}
						}

						&.detail-second {
							span {
								display: inline-block;

								&:nth-of-type(1) {
									margin-right: 6px;
								}
							}
						}
					}
				}
			}
		}
	}
</style>